body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
fieldset,
img,
abbr {
  border: 0;
}
address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}
ul li {
  list-style: none;
}
caption,
th {
  text-align: left;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
sup {
  vertical-align: text-top;
}
sub {
  vertical-align: text-bottom;
}
input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}
legend {
  color: #000;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}
img {
  max-width: 100%;
  height: auto;
}
.pseudo:before,
.pseudo:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
}
.center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.gpu {
  transform: translate3d(0, 0, 0);
}
@font-face {
  font-family: 'Bangers';
  font-style: normal;
  font-weight: 400;
  src: local('Bangers Regular'), local('Bangers-Regular'), url(https://fonts.gstatic.com/s/bangers/v11/FeVQS0BTqb0h60ACH55Q3Q.ttf) format('truetype');
}
.canvas {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
  opacity: 0.75;
}
body {
  background: #b62b37;
  overflow-x: hidden;
}
svg {
  display: none;
}
.hero {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 230px;
  width: 100%;
  text-align: center;
  pointer-events: none;
  font-weight: bold;
  font-family: 'Bangers', cursive;
  letter-spacing: 12px;
  text-shadow: 1px 1px 50px rgba(0, 0, 0, 0.377);
  opacity: 0.95;
}
.hero_title {
  position: relative;
  text-transform: uppercase;
}
.hero_title__pref {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
  letter-spacing: 0;
  position: relative;
  position: absolute;
  top: 30px;
  right: -55px;
}
.dg.ac {
  z-index: 1000000!important;
}
.content {
  margin-top: 100vh;
  background: #1a1a1a;
  color: #f0f0f0;
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
  padding: 250px 0;
}
.content_subtitle {
  font-size: 25px;
  font-weight: 300;
  margin-bottom: 15px;
}
.content_title {
  font-size: 50px;
}
.download_link {
  color: #b62b37;
  text-transform: uppercase;
  font-weight: bold;
}
.side {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100px;
  z-index: 10000;
  border-right: 1px solid rgba(197, 197, 197, 0.808);
  pointer-events: none;
}
.side:after {
  content: attr(data-text);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transform: translate(-50%, -50%) rotate(-90deg);
  font-family: Arial;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 12px;
  letter-spacing: 1px;
}
.nav {
  z-index: 10000;
  position: absolute;
  top: 20px;
  right: 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
}
.nav_link {
  color: white;
  opacity: 0.3;
  margin-left: 15px;
}
.nav_link:hover {
  opacity: 1;
}
